.scores__container {
	background: #ccc;
	border-radius: 10px;
	box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
	margin: 50px;
	padding: 20px;
}

.scores__table {
	width: 100%;
	margin: 0 auto;
	background: #fff;
	border-radius: 10px;
	padding: 20px;
	text-align: center;
	margin-bottom: 20px;
}
